ks-order-guest {
    display: block;
    padding: 30px 30px 20px 30px;
    text-align: center;

    border-bottom: #cccccc 1px solid;
}

@media (min-width: 1200px) {
    ks-order-guest {
        padding: 40px 40px 30px 40px;
    }
}

ks-order-guest .contents {
    max-width: 500px;
    margin: auto;
}

ks-order-guest h1 {
    font-weight: 700;
    font-size: 28px;
}